$dark: (
  #080808,
  #181818,
  #282828,
  #383838,
  #484848,
  #585858,
  #686868,
  #787878,
);
$light: (
  #f8f8f8,
  #e8e8e8,
  #d8d8d8,
  #c8c8c8,
  #b8b8b8,
  #a8a8a8,
  #989898,
  #888888,
);
$primary: (
  #4d1900,
  #802a00,
  #b33b00,
  #e64C00,
  #ff661a,
  #ff884d,
  #ffaa80,
  #ffccb3,
);

$brights: 8;
$colors: (
  dark: $dark,
  light: $light,
  primary: $primary,
);

.bg-no {
  background: none;
}

@each $color-k, $color in $colors {
  @for $i from 1 through $brights {
    $val: nth($color, $i);
    @include hov(bg-#{$color-k}#{$i}) {
      background-color: $val;
    }
    @include hov(bdr-#{$color-k}#{$i}) {
      border-color: $val;
    }
    @include hov(#{$color-k}#{$i}) {
      color: $val;
    }
  }
}
